@import url(https://fonts.googleapis.com/css2?family=Domine:wght@400;500;600;700&family=Libre+Baskerville:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg: #faece9;
  --brown-light: #946245;
  --brown-dark: #381802;
  --footer-bg: #f7e1d2;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: 0.3s ease;
  font-family: 'Domine', 'Libre Baskerville' serif;
}

html, body {
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  font-size: 16px;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  display: inline-block;
}

p {
  font-size: 1rem;
}

.App {
  min-height: 100vh;
  color: #381802;
  color: var(--brown-dark);
  background: url(/static/media/noise-bg.1f259e4c.png) no-repeat center center/cover;
  background-color: #faece9;
  background-color: var(--bg);
  -webkit-animation: noise 0.2s infinite linear;
          animation: noise 0.2s infinite linear;
}

@-webkit-keyframes noise {
  0% {
    background-position: left;
  }

  100% {
    background-position: right;
  }
}

@keyframes noise {
  0% {
    background-position: left;
  }

  100% {
    background-position: right;
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

@media(max-width: 769px) {
  html {
    font-size: 14px
  }

  .container {
    padding: 0 0.5rem;
  }
}


.nav-wrapper {	
	border-bottom: 1px solid var(--brown-dark);
	z-index: 100;
}

.nav {
	padding: 0.5rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-family: 'Domine', serif;
	overflow: hidden;
}

.nav .cart-btn {
	font-size: 1.1rem;
	color: var(--brown-dark);
	text-transform: uppercase;
}

.nav .logo img {
	width: 5rem;
}

.nav .menu-btn {
	background: transparent;
	border: none;
	outline: none;
	font-family: inherit;
	cursor: pointer;
	display: inline-flex;
	align-items: center;


	font-size: 1.1rem;
	color: var(--brown-dark);
	text-transform: uppercase;
}

.nav .menu-btn .lines {
	display: inline-flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	height: 100%;
	margin-right: 0.5rem;
}

.nav .menu-btn .lines .line {
	height: 2px;
	width: 25px;
	background-color: var(--brown-dark);
	margin: 3px;
}

.nav .menu-btn.active .line-1 {
	-webkit-transform: rotate(45deg) translateX(20%);
	        transform: rotate(45deg) translateX(20%);
}

.nav .menu-btn.active .line-2 {
	-webkit-transform: rotate(-45deg) translate(20%, -15%);
	        transform: rotate(-45deg) translate(20%, -15%);
}

.nav-links {
	z-index: 100;
	position: absolute;
	top: 3rem;
	right: 0;
	-webkit-transform: translateX(110%);
	        transform: translateX(110%);
	width: 25rem;
	height: 100vh;
	display: flex;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
	background-color: var(--bg);
	list-style: none;
	padding: 3rem 2rem;
	transition: 0.5s ease;
}

.nav-links.active {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}

.nav-links .nav-link {
	text-transform: uppercase;
	font-family: 'Domine', serif;
	font-size: 1.2rem;
	display: inline-block;
}

.nav-links .nav-link a {
	color: var(--brown-dark);
}
.header {
	min-height: calc(100vh - 3rem);
	padding: 1rem;
	border-bottom: 1px solid var(--brown-dark);
	padding-top: 4rem;
	background-image: url(/static/media/fashion-leaves.9c7d310e.png);
	background-position: bottom right;
	background-repeat: no-repeat;
	background-size: contain;
}

.header__content h2 {
	font-family: 'Libre Baskerville', serif;
	font-size: 4.5rem;
	font-weight: 700;
	line-height: 1;
	color: var(--brown-light);
	text-transform: uppercase;
	margin-bottom: 1rem;
}

.header__content h2 span {
	color: transparent;
	-webkit-text-stroke: 1px var(--brown-light);
}

.header__content p {
	font-size: 1.1rem;
	font-family: 'Domine', serif;
	margin-bottom: 1.5rem;
}

.explore-btn {
	width: 70px;
	height: 70px;
	border: 1px solid var(--brown-dark);
	border-radius: 50%;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	padding: 1rem;
	font-size: 0.8rem;
	font-weight: 500;
	color: var(--brown-dark);
}



@media (max-width: 769px) {
	.header {
		min-height: 70vh;
		background-position: bottom left;
		background-size: cover;
	}

	.explore-btn {
		width: 60px;
		height: 60px;
		padding: 0.5rem;
	}
}

@media (max-width: 500px) {
	.header {
		padding-top: 3rem;
	}

}
#know_us {
	margin-bottom: 5rem;
}

.ribbon {
	margin: 1rem 0 5rem;
	overflow: hidden;
}

.ribbon img {
	display: inline-block;
	height: 3rem;
	width: 150vw;
	object-fit: cover;
	border-top: 1px solid var(--brown-dark);
	border-bottom: 1px solid var(--brown-dark);
	-webkit-animation: slide 10s infinite linear;
	        animation: slide 10s infinite linear;
}

@-webkit-keyframes slide {
	0% {
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}

	100% {
		-webkit-transform: translate3d(-30%, 0, 0);
		        transform: translate3d(-30%, 0, 0);
	}
}

@keyframes slide {
	0% {
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}

	100% {
		-webkit-transform: translate3d(-30%, 0, 0);
		        transform: translate3d(-30%, 0, 0);
	}
}

