#face__products .title {
	font-size: 3.5rem;
	font-family: 'Domine', serif;
	text-transform: uppercase;
	text-align: center;
	margin-bottom: 2rem;
}

#face__products .face__product {
	margin-bottom: 7rem;
}

#face__products .face__product .product__banner {
	min-height: 90vh;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	margin-bottom: 2rem;
	padding-right: 2rem;
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
}

#face__products .face__product__1 .product__banner {
	background-image: url(/static/media/glow.4c643f68.png);
}

#face__products .face__product__2 .product__banner {
	background-image: url(/static/media/product-creamy.d8685832.png);
}

#face__products .face__product__3 .product__banner {
	background-image: url(/static/media/product-rose.2f789de6.png);
}

#face__products .face__product .product__title {
	color: var(--bg);
	font-size: 4rem;
    font-weight: 300;
    margin-bottom: 2rem;
    font-family: 'Domine', serif;
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem);
}

#face__products .face__product__2 .product__title,
#face__products .face__product__3 .product__title {
	color: var(--brown-dark);
}

#face__products .face__product .product__content {
	display: grid;
	grid-template-columns: 3fr 2fr;
}

#face__products .face__product .product__content .product__details {
	color: var(--brown-light);
    padding-left: 2rem;
    border-left: 1px solid var(--brown-light);
    width: 80%;
    font-family: 'Libre Baskerville', serif;
    font-size: 1rem;
    line-height: 1.3;
}



@media (max-width: 769px) {
	#face__products .title {
		font-size: 3rem;
	}

	#face__products .face__product .product__banner {
		min-height: 80vh;
		padding-right: 0;
		justify-content: center;
	}

	#face__products .face__product .product__title {
		font-size: 3rem;
	    -webkit-transform: translateX(0);
	            transform: translateX(0);
	}

	#face__products .face__product .product__content {
		grid-template-columns: 1fr 1fr;
	}
}


@media (max-width: 500px) {
	#face__products .title {
		font-size: 3rem;
	}

	#face__products .face__product .product__banner {
		min-height: 60vh;
		justify-content: flex-start;
		padding-left: 1rem;
	}

	#face__products .face__product .product__title {
		font-size: 2.8rem;
	}

	#face__products .face__product .product__content {
		grid-template-columns: 1fr;
		padding: 0 1rem;
	}
}


	
footer {
	padding: 3rem 1rem;
	background-color: var(--footer-bg);
	margin-top: 5rem;
	color: var(--brown-light);
}

footer .logo {
	margin-bottom: 2rem;
	display: inline-block;
}

footer .logo img {
	width: 6rem;
	object-fit: cover;
}

.footer__main {
	display: flex;
	grid-gap: 3rem;
	gap: 3rem;
	margin-bottom: 2rem;
}

.footer__main .footer__text {
	flex: 1 1 50%;
}

.footer__main .footer__text p {
	line-height: 1.4;
	font-family: 'Libre Baskerville', serif;
	width: 80%;
}

.footer__main .footer__links {
	flex: 1 1 50%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
}

.footer__main .footer__links ul {
	list-style: none;
}

.footer__main .footer__links ul h4 {
	font-size: 1.1rem;
	text-transform: uppercase;
	font-family: 'Libre Baskerville', serif;
}

.footer__main .footer__links ul li {
	margin-bottom: 0.5rem;
}

.footer__main .footer__links ul li a {
	color: inherit;
	font-family: 'Libre Baskerville', serif;
}

footer .copy {
	font-size: 1rem;
}




@media (max-width: 769px) {
	footer {
		padding: 2rem 1rem;
	}

	.footer__main {
		display: grid;
		grid-template-columns: 1fr;
		justify-items: start;
		grid-gap: 1.5rem;
		gap: 1.5rem;
	}

	.footer__main .footer__links {
		width: 100%;
		display: grid;
		grid-gap: 1rem;
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (max-width: 500px) {

	.footer__main {
		grid-template-columns: 1fr;
	}

	.footer__main .footer__links {
		grid-template-columns: 1fr;
	}
}
